* {
  font-family: 'Josefin Sans', sans-serif;
  text-align: center;
}

/* ---HEADER--- */

h1 {
  font-size: 4rem;
}

.butcher-pig-image {
  margin: 1rem;
  width: 14rem;

}

/* ---BODY--- */

button {
  border-radius: 0.9rem;
  margin: 1rem;
  padding: 1rem;
}
button:Hover {
  background-color: cornflowerblue;
  cursor: pointer;
}

.body-container {
  padding-bottom: 3rem;
}

.page-container {
  min-height: 100vh;
  position: relative;
  background-color: rgb(201, 143, 185);
}


.input-section {
  background-color: grey;
  border-radius: .5rem;
  color: #fff;
  margin: 1rem;
  padding: 1rem;
}

.user-input {
  border-radius: .5rem;
  font-size: 1rem;
  height: 3rem;
  width: 30rem;
}

/* ---FOOTER--- */

footer {
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 3rem;
  border: solid 3px black;
}

@media only screen and (max-width: 650px) {
  h1 {
    font-size: 2rem;
  }
  .user-input {
    font-size: 1rem;
    height: 5rem;
    padding: 1rem;
    text-align: center;
    width: 90%;
  }
}
